/* General container styling */
.settings-container {
    margin: 2rem auto;
    max-width: 600px;
    padding: 2rem;
    border-radius: 10px;
    background-color: #f7f7f7;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.settings-title {
    font-size: 2rem;
    font-weight: bold;
    color: rgba(0, 72, 90, 0.911);
    text-align: center;
    margin-bottom: 1.5rem;
}

/* Profile section */
.profile-section {
    display: flex;
    align-items: center;
    gap: 1rem;
    margin-bottom: 2rem;
}

.profile-pic-wrapper {
    position: relative;
    width: 80px;
    height: 80px;
    border-radius: 50%;
    overflow: hidden;
    border: 2px solid #57b7e0;
    background-color: #ddd;
}

.profile-pic {
    width: 80px;
    height: 80px;
    object-fit: cover;
}

.profile-pic-placeholder {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    font-size: 0.6rem;
    color: #888;
}

.profile-info h3 {
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
}

.profile-info p {
    font-size: 0.9rem;
    color: #292424;
}

/* Buttons */
.btn-edit, .btn-change-password {
    padding: 8px 15px;
    background-color: rgba(0, 72, 90, 0.911);
    border: none;
    color: #fff;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s;
    display: flex;
    align-items: center;
    gap: 0.5rem;
}

.btn-edit:hover, .btn-change-password:hover {
    background-color: #1f90b0;
}

/* Notifications toggle */
.notifications-group {
    display: flex;
    flex-direction: column;
    gap: .5rem;
    justify-content: space-between;
    align-items: left;
    margin-bottom: 1.5rem;
}

.toggle-switch {
    position: relative;
    width: 50px;
    height: 25px;
    background-color: #ccc;
    border-radius: 25px;
    cursor: pointer;
}

.toggle-switch input[type="checkbox"] {
    display: none;
}

.toggle-switch label {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #ccc;
    border-radius: 25px;
    cursor: pointer;
    transition: background-color 0.3s;
}

.toggle-switch label::after {
    content: '';
    position: absolute;
    width: 20px;
    height: 20px;
    background-color: white;
    border-radius: 50%;
    top: 50%;
    transform: translateY(-50%);
    transition: transform 0.3s;
}

.toggle-switch input[type="checkbox"]:checked + label {
    background-color: #0d678d;
}

.toggle-switch input[type="checkbox"]:checked + label::after {
    transform: translate(25px, -50%);
}

/* Modal styles */
.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
}

.modal {
    background-color: #fff;
    border-radius: 10px;
    padding: 2rem;
    width: 100%;
    max-width: 350px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
}

.modal h3 {
    font-size: 1.5rem;
    margin-bottom: 1rem;
    color: rgba(0, 72, 90, 0.911);
}

.modal-form {
    display: flex;
    flex-direction: column;
    gap: 1.25rem;
    font-size: 1rem;
    font-weight: 550;
    color: #001f2c;
}

.modal-input {
    padding: 5px;
    width: 100%;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 1rem;
    color: rgb(32, 31, 31);
}

.modal-buttons {
    display: flex;
    justify-content: space-between;
}

.btn-save, .btn-cancel {
    padding: 8px 15px;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s;
}

.btn-save {
    background-color: rgba(0, 72, 90, 0.911);
    border: none;
    color: #fff;
}

.btn-save:hover {
    background-color: #1f90b0
}

/* Media queries */
@media only screen and (max-width: 480px) {
    .settings-container {
        margin: 2rem 2rem;
        max-width: 600px;
        padding: 2rem;
        border-radius: 10px;
        background-color: #f7f7f7;
        box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    }       

    .settings-title {
        font-size: 1.5rem;
        font-weight: bold;
        color: rgba(0, 72, 90, 0.911);
        text-align: center;
        margin-bottom: 1.5rem;
    }

    .profile-pic-wrapper {
        position: relative;
        width: 50px;
        height: 50px;
        border-radius: 50%;
        overflow: hidden;
        border: 2px solid #57b7e0;
        background-color: #ddd;
    }
    
    .profile-pic {
        width: 60px;
        height: 60px;
        object-fit: cover;
    }
}